// create footer content

import { sanityFetch } from "@/sanity/lib/live";
import Link from "next/link";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";

const FOOTER_QUERY = `*[_type == "siteSettings" && _id == "siteSettings"][0]{ siteTitle, logo, footer,address,phone }`;

export default async function Footer() {

    const { data: siteSettingsFooter } = await sanityFetch({ query: FOOTER_QUERY });

    const { siteTitle, footer, logo }: {
        siteTitle?: string;
        footer?: { socialLinks?: { url: string; name: string }[] };
        logo?: { asset?: { _ref: string }; url?: string; alt?: string; width?: number; height?: number };

    } = siteSettingsFooter;
    const logo1 = siteSettingsFooter?.footer?.footerLogo1;
    const logo2 = siteSettingsFooter?.footer?.footerLogo2;
    const logo3 = siteSettingsFooter?.footer?.footerLogo3;


    return (
        
        <div className="bg-gray-100 p-8 ">
            
            <footer className="wp-block-template-part  w-[1200]px mx-auto">
                <div className="wp-block-group alignfull mdFooter has-global-padding is-layout-constrained">
                    <div className="flex flex-wrap justify-between p-4">
                        {/* Left Section */}
                        <div className=" md:w-1/2">
                            {/* Logo */}
                            {logo?.asset && (
                                <a href="/" aria-label={siteTitle}>
                                    <Image
                                        src={logo?.asset?._ref ? urlFor({ asset: { _ref: logo.asset._ref, _type: "reference" } }).url() || '' : logo?.url || ''}
                                        alt={logo?.alt || 'Post Image'}
                                        width={logo?.width || 800}
                                        height={logo?.height || 600}
                                        priority
                                    />
                                </a>
                            )}

                            {/* Address */}
                            <div className="mt-4 text-amber-950">
                                <div className="flex items-center mb-2">
                                    <img
                                        src="/icons/footer-location-icon.svg" // Replace with your own asset if needed
                                        alt="Location Icon"
                                        className="w-5 h-5 mr-2"
                                    />
                                    <p>{siteSettingsFooter?.footer?.address}</p>
                                </div>

                                <div className="flex items-center">
                                    <img
                                        src="/icons/footer-phone-icon.svg" // Replace with your own asset if needed
                                        alt="Phone Icon"
                                        className="w-5 h-5 mr-2"
                                    />
                                    <a href="tel:{phone}">{siteSettingsFooter?.footer?.phone}</a>
                                </div>
                            </div>

                            {/* Social Links */}
                            {footer?.socialLinks?.length > 0 && (
                                <ul className="flex space-x-4 mt-4">
                                    {footer.socialLinks.map((link) => (
                                        <li key={link.url}>
                                            <a href={link.url} target="_blank" rel="noreferrer noopener">
                                                <i className="fab fa-twitter"></i>
                                                <i className="fas fa-user"></i>
                                                {link.name}
                                            </a>
                                        </li>
                                    ))}
                                </ul>
                            )}
                        </div>

                        {/* Right Section */}
                        <div className=" md:w-1/2 flex justify-between">
                            {/* Careers Links */}
                            <div>
                                <h4>Careers</h4>
                                <ul className="space-y-2">
                                    <li><a href="#perks">Perks</a></li>
                                    <li><a href="/current-openings">Current Openings</a></li>
                                    <li><a href="/submit-resume">Submit Resume</a></li>
                                    {/* Add more links as needed */}
                                </ul>
                            </div>

                            {/* Company Links */}
                            <div>
                                <h4>Company</h4>
                                <ul className="space-y-2">
                                    <li><a href="https://www.multidots.com/about-multidots/" target="_blank" rel="noopener">About Us</a></li>
                                    <li><a href="https://www.multidots.com/case-studies/" target="_blank" rel="noopener">Case Studies</a></li>
                                    {/* Add more links as needed */}
                                </ul>
                            </div>
                        </div>
                    </div>

                    {/* Bottom Logos */}
                    <div className="flex justify-center gap-8 mt-8 footer-logos">
                        <a href="https://www.inc.com/profile/multidots" target="_blank" rel="noopener">
                            {logo1?.asset && (
                            
                                    <Image
                                        src={logo1?.asset?._ref ? urlFor({ asset: { _ref: logo1.asset._ref, _type: "reference" } }).url() || '' : logo1?.url || ''}
                                        alt={logo1?.alt || 'Post Image'}
                                        width={logo1?.width || 120}
                                        height={logo1?.height || 120}
                                        priority
                                    />
                            )}
                        </a>
                        <a href="https://www.multidots.com/wpvip/">
                            {logo2?.asset && (

                                <Image
                                    src={logo2?.asset?._ref ? urlFor({ asset: { _ref: logo2.asset._ref, _type: "reference" } }).url() || '' : logo2?.url || ''}
                                    alt={logo2?.alt || 'Post Image'}
                                    width={logo2?.width || 120}
                                    height={logo2?.height || 120}
                                    priority
                                />
                            )}                        </a>
                        <a href="https://www.greatplacetowork.in/">
                            {logo3?.asset && (

                                <Image
                                    src={logo3?.asset?._ref ? urlFor({ asset: { _ref: logo3.asset._ref, _type: "reference" } }).url() || '' : logo3?.url || ''}
                                    alt={logo3?.alt || 'Post Image'}
                                    width={logo3?.width || 120}
                                    height={logo3?.height || 120}
                                    priority
                                />
                            )}                        </a>
                    </div>

                    {/* Copyright */}
                    <p className="text-center mt-8 text-sm">Â© {new Date().getFullYear()}{siteSettingsFooter?.footer?.footerText}</p>
                </div>
            </footer>
            <div className="flex items-center justify-between">
                <nav>
                    <ul className="flex space-x-4">
                        {footer?.socialLinks?.map((socialLink: { url: string; name: string }) => (
                            <li key={socialLink.url}>
                                <Link href={`/${socialLink.url}`}>
                                    {socialLink.name}
                                </Link>
                            </li>
                        ))}
                    </ul>
                </nav>
            </div>
        </div>
    );
}